.sidebar {
  background-image: url(/source/img/menu_bg.png);
  min-width: 300px;
  max-width: 300px;
  padding: 0;
  height: 100%;
  overflow: hidden;
  display: flex;
  flex-flow: column;
  justify-content: space-between; }

.sidebar-links-list {
  display: flex;
  flex-flow: column;
  justify-content: space-around;
  list-style: none; }

.links-item {
  padding: 0.5rem; }
  .links-item a {
    text-decoration: none;
    color: #bdbdbd;
    font-size: 1rem; }

.sidebar-phone {
  margin-bottom: 2.8125rem; }

.sidebar-phone-number {
  font-weight: 300;
  color: #fefefe;
  font-size: 1.25rem;
  padding: 1.25rem 2.3125rem 0;
  margin: 0; }

.sidebar-phone-text {
  color: #c57c40;
  font-size: 1rem;
  border-bottom: 1px dashed #c57c40;
  margin-left: 2.3125rem; }

.mmenu {
  display: none; }

.sidebar-logo {
  text-align: center;
  position: relative;
  display: flex;
  flex-flow: column;
  justify-content: space-between;
  background-image: url(/source/img/logo_line.png);
  background-repeat: no-repeat;
  background-position: 47px 117px; }

.sidebar-logo-title {
  display: inline-block;
  color: #f4f4f4;
  font-family: "Georgia";
  font-size: 3rem;
  text-transform: uppercase;
  text-align: center;
  border: 3px solid #976740;
  padding: 0.8125rem 0.625rem 0.5rem;
  margin: 1.875rem auto 0; }

.sidebar-logo-text {
  display: inline-block;
  font-family: "Georgia";
  font-size: 0.9375rem;
  color: #f5f5f5;
  text-transform: uppercase;
  margin: 0;
  position: absolute;
  bottom: 80px;
  right: 50px;
  z-index: 3;
  background-image: url(/source/img/menu_bg.png);
  background-repeat: no-repeat;
  padding: 0 0.5rem; }

.sidebar-logo-description {
  color: #aaa9a9;
